*{
  box-sizing: border-box;
}
body{
  padding: 0;
  margin: 0;
  font-family: Inter, sans-serif;
  background:#282D35;
}
.nav-bar{
  display: flex;
  align-items: center;
  background: #21222A;
  padding: 30px 25px;
  height: 90px;
}
.nav-item1, .nav-item2{
  margin: 0;
}
.nav-item1 {
  color: #00D8FF; 
  font-size: 22.25px;
  font-weight: 700;
}
.nav-item2 {
  color: #DEEBF8;
  font-weight: 600;
  margin-left: auto;
}
.react-logo{
  margin-right: 7px;
  height: 30px;
}
.main-content{
  box-sizing: border-box;
  padding: 20px;
  height: 100%;
  width: 100%;
  bottom: 0;
  background-image: url(./react-icon-large.png);
  background-repeat: no-repeat;
  background-position: right 100%;
}
.header{
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  letter-spacing: -0.05em;
}
ul{
  list-style: none;
}
ul li::before{
  content: "\2022";
  color: #61DAFB;
  font-weight: bolder;
  width: 20px;
  font-size: 30px;
  line-height: 30px;
  display: inline-block;
}
.list{
  color: #FFF;
  line-height: 20px;
  font-weight: 400;
}


